.App {
  padding: 1px 0px 45px 0px;
}

.wrapper {
  position: relative;
}

.znakomstvo {
  position: absolute;
  text-align: end;
  top: 10%;
  right: 5%;
  max-width: 700px;
  border-radius: 10px;
  font-size: 130%;
  color: #1aaf3f;
  font-weight: bold;
}

.playbober {
  display: flex;
}

.score {
  width: 80px;
  height: 80px;
  color: #1aaf3f;
  font-size: 300%;
  font-weight: bold;
  border-radius: 50%;
  border: #0c7116 solid 5px;
  padding: 2px;
  text-align: center;
}

.lifes {
  background-color: white;
  padding: 5px;
  width: 250px;
  height: 50px;
  border: #0c7116 solid 5px;
  font-size: 200%;
  display: flex;
  margin-left: 10px;
  margin-top: 10px;
  border-radius: 20px;
}

.life img {
  margin: 5px;
  width: 40px;
  height: 40px;
}

.layer {
  position: fixed;
  top: 10%;
  left: 40%;
  width: 400px;
  border-radius: 10px;
  font-size: 400%;
  font-weight: bold;
  color: #1aaf3f;
}

.mouseblock {
  border: 1px solid rgba(0, 0, 0, 0);
  height: 500px;
}

.error {
  border: 1px solid rgba(0, 0, 0, 0);
  position: absolute;
  top: 10%;
  left: 50%;
  width: 700px;
  border-radius: 10px;
  font-size: 130%;
  color: #1aaf3f;
  font-weight: bold;
}

@media (max-width: 1300px) {
  .znakomstvo {
    max-width: 600px;
    border-radius: 10px;
    font-size: 120%;
  }
}
